.page {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-grow: 1;
  flex-direction: column;
  padding: 30px 0;
}
.content {
  position: relative;
  margin-bottom: 120px;

  &::before {
    content: '';
    position: absolute;
    display: block;
    border: 24px solid #ebedf5;
    transform: rotate(30deg);
    width: 174px;
    height: 140px;
    bottom: -34px;
    right: 36px;
  }
  &::after {
    content: '';
    position: absolute;
    display: block;
    border: 24px solid #ebedf5;
    transform: rotate(30deg);
    width: 174px;
    height: 140px;
    top: -48px;
    left: 38px;
  }
}
.contentInner {
  padding: 80px 92px 64px;
  background: #ffffff;
  box-shadow: 0px 12px 24px rgba(87, 111, 172, 0.08);
  border-radius: 12px;
  max-width: 610px;
  position: relative;
  z-index: 1;
}

.title {
  font-weight: bold;
  font-size: 32px;
  line-height: 39px;
  text-align: center;
  color: #1e0e33;
  margin-bottom: 24px;
}
.descr {
  font-size: 24px;
  line-height: 29px;
  text-align: center;
  color: #64697a;
  margin-bottom: 48px;
}
.actions {
  text-align: center;
}
.btn {
  min-width: 140px;
  margin-right: 16px;

  &:last-child {
    margin-right: 0;
  }
}
.innetBtn {
  display: flex;
  align-items: center;
  justify-content: center;
}
.innetBtnIcon {
  margin-right: 8px;
}
.innetBtnText {
  font-weight: 600;
  font-size: 12px;
  line-height: 16px;
}

.bottom {
  display: flex;
  width: 100%;
}
.bottomLeft {
  flex: 0 0 50%;
  max-width: 50%;
  padding-right: 44px;
  position: relative;
  text-align: right;

  &::before {
    content: '';
    background-color: #49597c;
    height: 9px;
    position: absolute;
    left: 0;
    right: 120px;
    top: 50%;
    transform: translateY(-50%);
  }
}
.bottomLeftIcon {
  position: relative;
  z-index: 1;
}
.bottomRight {
  flex: 0 0 50%;
  max-width: 50%;
  padding-left: 44px;
  position: relative;
  text-align: left;

  &::before {
    content: '';
    background-color: #49597c;
    height: 9px;
    position: absolute;
    right: 0;
    left: 120px;
    top: 50%;
    transform: translateY(-50%);
  }
}
.bottomRightIcon {
  position: relative;
  z-index: 1;
}
